<style scoped>
.layout {
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
}

.layout-breadcrumb {
    padding: 10px 15px 0;
}

.layout-content {
    min-height: 1000px;
    margin: 15px;
    overflow: hidden;
    background: #fff;
    border-radius: 4px;
    padding: 20px;
}

.layout-content-main {
    padding: 10px;
}

.layout-copy {
    text-align: center;
    padding: 10px 0 20px;
    color: #9ea7b4;
}

.layout-menu-left {
    background: #464c5b;
}

.layout-header {
    height: 60px;
    background: #fff;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .1);
}

.layout-logo-left {
    width: 90%;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    margin: 15px auto;
}

.layout-ceiling-main a {
    color: #9ba7b5;
}

.layout-hide-text .layout-text {
    display: none;
}

.ivu-col {
    transition: width .2s ease-in-out;
}
</style>
<template>
    <div class="layout" :class="{'layout-hide-text': spanLeft < 5}">
        <Row type="flex">
            <i-col :span="spanLeft" class="layout-menu-left">
                <Affix>
                    <Menu active-name="1" theme="dark" width="auto" style="text-align: left">
                        <Menu-item name="1">
                            <router-link to="/layout/preview">
                                <Icon type="play" :size="iconSize"></Icon>
                                <span class="layout-text">开发预览</span>
                            </router-link>
                        </Menu-item>
                        <Menu-item name="2">
                            <router-link to="/layout/devcli">
                                <Icon type="hammer" :size="iconSize"></Icon>
                                <span class="layout-text">CLI工具使用快速上手</span>
                            </router-link>
                        </Menu-item>
                        <Menu-item name="3">
                            <router-link to="/layout/devexpress">
                                <Icon type="social-nodejs" :size="iconSize"></Icon>
                                <span class="layout-text">Express应用快速上手</span>
                            </router-link>
                        </Menu-item>
                        <Menu-item name="4">
                            <router-link to="/layout/devkoa">
                                <Icon type="social-nodejs" :size="iconSize"></Icon>
                                <span class="layout-text">Koa应用快速上手(构建中...)</span>
                            </router-link>
                        </Menu-item>
                        <Menu-item name="5">
                            <router-link to="/layout/devcomponents">
                                <Icon type="ios-list" :size="iconSize"></Icon>
                                <span class="layout-text">组件一览</span>
                            </router-link>
                        </Menu-item>
                    </Menu>
                </Affix>
            </i-col>
            <i-col :span="spanRight">
                <div class="layout-breadcrumb">
                    <Breadcrumb>
                        <Breadcrumb-item>
                            <router-link to="/">首页</router-link>
                        </Breadcrumb-item>
                    </Breadcrumb>
                </div>
                <div class="layout-content">
                    <router-view></router-view>
                </div>
                <div class="layout-copy">
                    2016-2017 &copy; cheneyxu
                </div>
            </i-col>
        </Row>
    </div>
</template>
<script>
export default {
    data() {
            return {
                spanLeft: 5,
                spanRight: 19
            }
        },
        computed: {
            iconSize() {
                return this.spanLeft === 5 ? 14 : 24;
            }
        },
        methods: {}
}
</script>



