<template>
    <div>
        <Row type="flex" justify="start">
            <h1>Express应用中间件使用指引</h1>
        </Row>
        <br/>
        <Row>
            <Timeline>
                <Timeline-item>
                    <p class="time">Step 1：开发环境准备，安装WEB核心服务 express 和ORM框架 sequelize，还有就是本例的中间件 express-xmodel </p>
                    注：不同的标准应用中间件组件需求的依赖可能不同，本例中的express-xmodel中间件依赖于 sequelize，所以需要安装 sequelize
                    <Card>
                        <p class="content">npm install express --save</p>
                        <p class="content">npm install sequelize --save</p>
                        <p class="content">npm install express-xmodel --save</p>
                    </Card>
                </Timeline-item>
                <Timeline-item>
                    <p class="time">Step 2：在{project}/src/sequelize/ 文件夹下新建 sequelize.js 文件，用于创建数据库连接</p>注：config是额外的配置文件,可选,具体使用参见 node config 模块
                    <Card>
                        <pre class="content">const Sequelize = require('sequelize')
const config = require('config')
const dbConfig = config.get('db')

let sequelize = new Sequelize(dbConfig.dbname, dbConfig.username, dbConfig.password, {
    host: dbConfig.host,
    dialect: 'mysql'
})

module.exports = sequelize</pre>
                    </Card>
                </Timeline-item>
                <Timeline-item>
                    <p class="time">Step 3：在{project}/src/model/ 文件夹下新建 UserModel.js 文件，用于映射实体对象和数据库表</p>
                    <Card>
                        <pre class="content">const Sequelize = require('sequelize');
const sequelize = require(__dirname + '/../sequelize/sequelize.js');

let UserModel = sequelize.define('test_user_model', {
    username: Sequelize.STRING,
    password: Sequelize.STRING
});

module.exports = UserModel;</pre>
                    </Card>
                </Timeline-item>
                <Timeline-item>
                    <p class="time">Step 4：在Express应用的入口程序文件，本例为{project}/app.js，使用标准应用中间件组件express-xmodel处理请求和响应</p>
                    <Card>
                        <p class="content">const xmodel = require('express-xmodel')</p>
                        <p class="content">app.use('/xmodel/', xmodel)</p>
                    </Card>
                </Timeline-item>
                <Timeline-item color="green">
                    <Icon type="trophy" slot="dot"></Icon>
                    <p class="time">Step 5：基于标准RESTful请求</p>
                    <Card>
                        <p class="content">Example：http://localhost:8080/xmodel/user_model/create</p>
                        <p class="content">{ "username":"cheney", "password":"123" }</p>
                    </Card>
                </Timeline-item>
            </Timeline>
        </Row>
        <Row>
            <h2>总结：</h2>
            <div class="normalfont">
                <p>1、<b>express-xmodel</b>应用中间件已经提供基础的<b>CRUD</b>处理，url请求中的<b>user_model</b>会自动映射<b>UserModel,js</b>文件</p>
                <br/>
                <p>2、这也就意味着在开发实体CRUD功能时，只需要编写定义一个Model文件，就完成了开发工作</p>
                <br/>
                <p>3、本例中间件的演示项目代码位于 <a href="https://github.com/cheneyweb/express-xmodel">express-xmodel演示项目</a>，<b>xserver</b>所有的演示项目代码都会力求最精简易懂：）</p>
            </div>
        </Row>
        <br/>
        <br/>
        <Row class="normalfont">
            <router-link to="/layout/devcomponents">查看更多应用服务中间件</router-link>
        </Row>
        <Row>
            <Back-top :height="100">返回顶部</Back-top>
        </Row>
    </div>
</template>
<script>
export default {

}
</script>
<style scoped>
.time {
    font-size: 16px;
    font-weight: bold;
}

.content {
    font-family: "Helvetica Neue";
    font-size: 14px;
    padding-left: 5px;
    color: #657180;
}

.normalfont {
    font-family: "Helvetica Neue";
    font-size: 15px;
}
</style>



